.all{
    padding: 30px 20px;
    display: flex;
    justify-content: center;
    flex-direction: column;

}
.userIcon{
    font-size: 5em;
    padding: 84px 0 0.8em;
    color: #212122;
}
.users{
    padding: 20px 0;
    display: flex;
    flex-wrap: wrap;
    max-width: 100%;
}
.userContainer{
    width: 31%;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    margin:  10px 7px;
    border-radius: 5px;
    background-color: rgba(33,33,34,0.1);
    align-items: center;
    overflow: hidden;
    padding-bottom: 15px;
    text-align: center;
}
.image_wrap{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: auto;
}
.name{
    font-weight: bolder;
    font-size: 1.5em;
    color: #212122;
    word-break: break-all;
    text-decoration: none;

}
.discription{
margin: 10px 0;
    word-break: break-all;
}
.discription span{
    display: block;
}
.image_wrap img{
    width: 100%;
    height: auto;
}
.userContainer button{
    width: 50%;
    background-color: rgba(220,20,60,0.9);
    font-size: 15px;
    border-radius: 5px;
    color: white;
    border: none;

}

.userContainer button.mode_Green{
    background-color: darkseagreen;
}

.userContainer button:disabled{
    background-color: #212122;
}

@media (max-width: 767px) {
    .userContainer{
        width: 46%;

    }
    .userIcon{
        font-size: 5em;
        padding: 1.6em 0 1.2em;
        color: #212122;
    }
}
@media (max-width: 479px){
    .all{
        padding: 30px 5px;
    }
    .userIcon{
        font-size: 5em;
        padding: 52px 0 0.5em;
        color: #212122;
    }
    .userContainer{
        margin: 10px 5px;
    }
    .name{
        font-size: 1.1em;
    }
    .discription span{
        font-size: 0.8em;
    }
    .userContainer button{
        width: auto;
    }
    .users{
        justify-content: center;
    }
}